<template lang="pug">
.side-left
  .aside-navbar(:class="[tabFocus == 'text' && 'active']" @click="tabFocus = 'text'")
    el-tooltip(effect="dark" content="文字" placement="right")
      <svg viewBox="0 0 24 24" id="eui-icon--text" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M20 5v14H4V5h16zm0-2a2 2 0 012 2v14a2 2 0 01-2 2H4a2 2 0 01-2-2V5a2 2 0 012-2h16zM8 8.5h8v2h-3v6h-2v-6H8v-2z"></path></svg>
  .navbar-cantainer(v-show="tabFocus == 'text'")
    .button-groups
      button(class="editor-button editor-text-style1" @click="addElement(1)") 添加标题 
      button(class="editor-button editor-text-style2" @click="addElement(2)") 添加副标题
      button(class="editor-button editor-text-style3" @click="addElement(3)") 添加正文
    .aside-navbar-close(@click="tabFocus=''")
  .aside-navbar(:class="[tabFocus == 'picture' && 'active']" @click="tabFocus = 'picture'")
    el-tooltip(effect="dark" content="图片" placement="right")
      <svg viewBox="0 0 24 24" id="eui-icon--photo" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M8.571 11.143a1.714 1.714 0 100-3.429 1.714 1.714 0 000 3.429zM14.024 11.965a2 2 0 012.518-.408l4.536 2.667-1.013 1.724-4.536-2.666-6.205 7.09-1.505-1.316 6.205-7.091z"></path><path fill-rule="evenodd" clip-rule="evenodd" d="M20 5H4v14h16V5zM4 3a2 2 0 00-2 2v14a2 2 0 002 2h16a2 2 0 002-2V5a2 2 0 00-2-2H4z"></path></svg>
  .navbar-cantainer(v-show="tabFocus == 'picture'")
    .scoller()
      .img-box.clear
        .img-block(:class="[index % 2 == 0 ? 'fl' : 'fr']" v-for="(item, index) in imgData" :key="index" @click="addImageElement(item)")
          img(:src="item.imgSrc")
    .aside-navbar-close(@click="tabFocus=''")
</template>
<script>

import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
.side-left
  width 60px
  box-sizing border-box
  .aside-navbar
    padding 10px 0
    text-align center
    margin-top 0px
    position relative
    svg
      width 24px
      height 24px
      fill #757e8a
      outline none
    &.active
      svg
        fill #6ccfff
    &.active:before
      opacity 1
    &:hover:before
      opacity 1
    &:before
      content ""
      position absolute
      top 50%
      left 0
      width 3px
      background-color #6ccfff
      height 40px
      border-radius 0 1px 1px 0
      -webkit-transform translateY(-50%)
      transform translateY(-50%)
      opacity 0
  .navbar-cantainer
    z-index 11
    position absolute
    width 280px
    height calc(100% - 51px)
    left 61px
    top 51px
    background-color #fff
    box-sizing border-box
    border-right 1px dotted #f5f5f5
    .scoller
      height 100%
      overflow auto
      .img-box
        width 100%
        box-sizing border-box
        .img-block
          width 50%
          box-sizing border-box
          padding 5px
          img
            width 100%
            display block
    .button-groups
      padding 20px 24px 14px
      .editor-button
        width 100%
        padding 0
        color #000
        background #f1f2f4
        border none
        border-radius 4px
        outline none
        cursor pointer
        margin-top 8px
      .editor-text-style1
        height 56px
        font-size 24px
        font-weight 600
      .editor-text-style2
        height 48px
        font-size 18px
        font-weight 500
      .editor-text-style3
        height 40px
        font-size 14px
        font-weight 500
    .aside-navbar-close
      display flex
      -webkit-box-align center
      -ms-flex-align center
      align-items center
      -webkit-box-pack center
      -ms-flex-pack center
      justify-content center
      position absolute
      left calc(100% + 1px)
      top 50%
      -webkit-transform translateY(-50%)
      transform translateY(-50%)
      width 20px
      height 64px
      cursor pointer
      -webkit-filter drop-shadow(4px 0 4px rgba(0,0,0,.02))
      filter drop-shadow(4px 0 4px rgba(0,0,0,.02))
      background-image url('data:image/png;base64,')
      background-repeat no-repeat
      background-size cover
      background-position 50%

</style>



