<template lang="pug">
  .top-bar.clear
    .icon-block.fl
    .undo-block.fl
      button(@click="undo", :disabled="!isVuexCaptureHaveUndo" style="margin-left: 10px") 撤销
      button(@click="redo", :disabled="!isVuexCaptureHaveRedo" style="margin-left: 10px") 重做
    span web编辑器在线Demo
</template>
<script>
import index from './index'
export default {
  ...index
}
</script>

<style lang="stylus" scoped>
.top-bar
  width 100%
  height 50px
  text-align center
  font-size 24px
  line-height 50px
  .icon-block
    width 50px
    height 100%
</style>



