
import { mapState } from 'vuex'
import { clone } from '@/utils/util'

export default {
  name: 'sideRight',
  data () {
    return {
    }
  },
  watch: {
    selectElementId (selectElementId) {
      if (selectElementId) {
        const elementStyle = Object.assign({}, this.elementStyle)
        this.opacityModel = elementStyle.opacity ? elementStyle.opacity * 100 : undefined
      }
    }
  },
  computed: {
    ...mapState({
      selectElementId: state => state.element.selectElementId,
      hoverElementId: state => state.hoverElementId,
      elementList: state => state.element.elementList,
      selectElementData () {
        const selectElementData = this.elementList.find(item => item.elementId == this.selectElementId)
        return selectElementData || {}
      },
      elementStyle () {
        if (this.selectElementData && this.selectElementData.style) {
          return this.selectElementData.style
        } else {
          return {}
        }
      },
      isTextBold () {
        return this.elementStyle.fontWeight == 'bold'
      },
      isTextItalics () {
        return this.elementStyle.fontStyle == 'italic'
      },
      isTextUnderline () {
        return this.elementStyle.textDecoration == 'underline'
      },
      isTextDeleteline () {
        return this.elementStyle.textDecoration == 'line-through'
      },
      isTextLeft () {
        return this.elementStyle.textAlign == 'left'
      },
      isTextCenter () {
        return this.elementStyle.textAlign == 'center'
      },
      isTextRight () {
        return this.elementStyle.textAlign == 'right'
      },
      isTextJustify () {
        return this.elementStyle.textAlign == 'justify'
      }
    }),
    elementOpacity () {
      return this.elementStyle.opacity ? this.elementStyle.opacity * 100 : undefined
    },
    elementFontSize () {
      return this.elementStyle.fontSize ? parseInt(this.elementStyle.fontSize) : 0
    },
    elementColor () {
      return this.elementStyle.color || ''
    },
    rotateDeg () {
      const transformStr = this.elementStyle && this.elementStyle.transform ? this.elementStyle.transform : ''
      const rotateNum = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
      return parseFloat(rotateNum) ? Math.ceil(parseFloat(rotateNum)) : 0
    }
  },
  methods: {
    opacityInput (val) {
      const style = Object.assign({}, this.selectElementData.style)
      style.opacity = parseFloat(val / 100).toFixed(2)
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(this.selectElementData, { style }) })
    },
    inputAttr (attr, val) {
      const style = clone(this.selectElementData.style)
      switch (attr) {
        case 'y':
          style.top = val ? parseInt(val) + 'px' : 0
          break
        case 'x':
          style.left = val ? parseInt(val) + 'px' : 0
          break
        case 'w':
          style.width = val ? parseInt(val) + 'px' : 0
          break
        case 'h':
          style.height = val ? parseInt(val) + 'px' : 0
          break
        case 'r':
          style.transform = val ? `rotateZ(${parseInt(val) % 360}deg)` : 0
          break
        case 'textBold':
          if (style.fontWeight == 'bold') {
            style.fontWeight = 'normal'
          } else {
            style.fontWeight = 'bold'
          }
          break
        case 'textItalics':
          if (style.fontStyle == 'italic') {
            style.fontStyle = 'normal'
          } else {
            style.fontStyle = 'italic'
          }
          break
        case 'textUnderline':
          if (style.textDecoration == 'underline') {
            style.textDecoration = 'none'
          } else {
            style.textDecoration = 'underline'
          }
          break
        case 'textDeleteline':
          if (style.textDecoration == 'line-through') {
            style.textDecoration = 'none'
          } else {
            style.textDecoration = 'line-through'
          }
          break
        case 'textLeft':
          style.textAlign = 'left'
          break
        case 'textCenter':
          style.textAlign = 'center'
          break
        case 'textRight':
          style.textAlign = 'right'
          break
        case 'textJustify':
          style.textAlign = 'justify'
          break
        case 'fontSize':
          style.fontSize = val ? parseInt(val) + 'px' : 0
        case 'color':
          style.color = val
      }
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign({}, this.selectElementData, { style }) })
    },
    inputAttrFocus () {
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: this.selectElementData, vuexCaptureMark: 'start' })
    },
    inputAttrBlur () {
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: this.selectElementData, vuexCaptureMark: 'end' })
    }
  }
}



