let num = 0
import imgData from './imgData'
import { clone } from '@/utils/util'

export default {
  name: 'sideLeft',
  data () {
    return {
      tabFocus: '',
      imgData
    }
  },
  methods: {
    addElement (type) {
      let defaultElement = {
        type: 'text-element',
        text: '默认元素',
        elementId: 0,
        style: {
          color: '#000',
          margin: 0,
          position: 'absolute',
          top: '250px',
          left: '350px',
          transform: 'none',
          opacity: 1,
          verticalAlign: 'middle',
          transform: 'rotateZ(0deg)',
          textAlign: 'center',
          fontSize: '24px',
          fontWeight: 'normal',
          fontStyle: 'normal',
          textDecoration: 'none',
          lineHeight: '1.5',
          width: '150px',
          height: '36px'
        }
      }
      switch (type) {
        case 1:
          defaultElement.text = '双击编辑标题'
          defaultElement.style.fontSize = '36px'
          defaultElement.style.height = '54px'
          defaultElement.style.width = '216px'
          break
        case 2:
          defaultElement.text = '双击编辑副标题'
          defaultElement.style.fontSize = '24px'
          defaultElement.style.height = '36px'
          defaultElement.style.width = '168px'
          break
        case 3:
          defaultElement.text = '双击编辑正文'
          defaultElement.style.fontSize = '14px'
          defaultElement.style.height = '21px'
          defaultElement.style.width = '84px'
          break
      }
      num++
      defaultElement.elementId = num
      this.$store.dispatch('setElementListItemCapture', { type: 'add', element: [defaultElement] })
    },
    addImageElement (item) {
      num++
      const ImageElement = clone(item)
      ImageElement.elementId = num
      this.$store.dispatch('setElementListItemCapture', { type: 'add', element: ImageElement })
    }
  }
}



