<template lang="pug">
  .main-container
    .header
      top-bar
    .content-container
      .layout-side-left
        side-left
      .layout-center-container
        router-view
      .layout-side-right
        side-right
</template>
<script>

import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
  .main-container
    width 100%
    height 100%
    display flex
    flex-direction column
    overflow overlay
    .header
      flex-shrink 0
    .content-container
      flex 1
      box-sizing border-box
      display flex
      flex-direction row
      justify-content space-between
      overflow hidden
      .layout-side-left
        height 100%
        overflow overlay
      .layout-center-container
        flex 1
        height 100%
        overflow overlay
      .layout-side-right
        height 100%
        overflow overlay
</style>



