<template lang="pug">
.side-right
  .modules(v-if="selectElementId")
    .block(v-if="selectElementData.type == 'text-element'")
      .des 文字选项
      el-row(class="text-center")
        el-col(:span="18")
          .text-font-size-block
            el-input-number(
              class="font-size-input" 
              :value="elementFontSize" 
              style="width: 100%" 
              controls-position="right" 
              size="large"
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @change="val => inputAttr('fontSize', val)" 
              :min="12"
            )
        el-col(:span="6")
          .text-font-color-block
            el-color-picker(@change="val => inputAttr('color', val)" :value="elementColor")
        el-col(:span="6")
          .icon-text-block(:class="[isTextBold ? 'active' : '']" @click="inputAttr('textBold')")
            el-tooltip(effect="dark" content="加粗" placement="top")
              <svg id="icon-text-bold" viewBox="0 0 1024 1024"><path d="M766.003921 470.384471a282.870276 282.870276 0 0 0 63.9978-182.393731A287.350122 287.350122 0 0 0 542.01162 0.00064H126.02592v1023.964801h463.984051A303.349572 303.349572 0 0 0 766.003921 470.384471zM286.02042 159.99514h255.9912a127.9956 127.9956 0 0 1 0 255.9912h-255.9912z m303.989551 703.975801H286.02042V575.980841h303.989551a143.99505 143.99505 0 0 1 0 287.9901z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextItalics ? 'active' : '']" @click="inputAttr('textItalics')")
            el-tooltip(effect="dark" content="斜体" placement="top")
              <svg id="icon-text-italic" viewBox="0 0 1024 1024"><path d="M832 0v128H690.56l-192 768H640v128H192v-128h142.08l192-768H384V0h448z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextUnderline ? 'active' : '']" @click="inputAttr('textUnderline')")
            el-tooltip(effect="dark" content="下划线" placement="top")
              <svg id="icon-text-underline" viewBox="0 0 1024 1024"><path d="M64 896h896v128H64zM512 832a352.64 352.64 0 0 1-352-352V0H320v480a192 192 0 0 0 384 0V0h160v480A352.64 352.64 0 0 1 512 832z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextDeleteline ? 'active' : '']" @click="inputAttr('textDeleteline')")
            el-tooltip(effect="dark" content="删除线" placement="top")
              <svg id="icon-strike-through" viewBox="0 0 1024 1024"><path d="M511.751481 384.069486l-12.154098-5.117515c-72.284897-29.42571-108.74719-67.807071-108.74719-116.423461a108.1075 108.1075 0 0 1 41.579808-86.358063 152.885755 152.885755 0 0 1 102.350296-35.182914 150.966687 150.966687 0 0 1 83.799305 20.470059 204.700592 204.700592 0 0 1 63.968935 76.762722L806.008582 166.575107A300.014306 300.014306 0 0 0 536.059676 0.255876 311.528714 311.528714 0 0 0 319.844675 75.739219a235.405681 235.405681 0 0 0-83.159615 185.509912A219.413447 219.413447 0 0 0 272.507663 384.069486H0v127.93787h1023.502961V384.069486zM631.373389 607.960759c5.117515 5.117515 10.874719 9.59534 15.352545 14.712855a137.53321 137.53321 0 0 1 24.947884 44.138565 173.995503 173.995503 0 0 1 8.315962 53.094216 160.562027 160.562027 0 0 1-46.697323 117.702841 158.642959 158.642959 0 0 1-117.063151 46.697323 156.084202 156.084202 0 0 1-135.614142-63.968935A255.87574 255.87574 0 0 1 344.152871 703.914162L191.906805 733.979561a349.910075 349.910075 0 0 0 104.269364 213.656243A308.969956 308.969956 0 0 0 511.751481 1023.758837a319.844675 319.844675 0 0 0 229.648477-89.556509A300.653995 300.653995 0 0 0 831.596156 709.671366a302.573063 302.573063 0 0 0-17.271612-101.710607z"></path></svg>
      el-row(class="text-center")
        el-col(:span="6")
          .icon-text-block(:class="[isTextLeft ? 'active' : '']" @click="inputAttr('textLeft')")
            el-tooltip(effect="dark" content="左对齐" placement="bottom")
              <svg id="icon-text-align-right" viewBox="0 0 1024 1024"><path d="M1042.24287605 806.57937513v117.83175114H276.33649907v-117.83175114h765.90637698z m0-235.66349956v117.83174977H-18.24287605v-117.83174977h1060.4857521z m0-235.66350091v117.83174977H276.33649907V335.25237466h765.90637698z m0-235.66350093v117.83175114H-18.24287605V99.58887373h1060.4857521z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextCenter ? 'active' : '']" @click="inputAttr('textCenter')")
            el-tooltip(effect="dark" content="居中对齐" placement="bottom")
              <svg id="icon-text-align-center" viewBox="0 0 1024 1024"><path d="M865.4952507 806.57937513v117.83175114H158.5047493v-117.83175114h706.9905014z m176.74762535-235.66349956v117.83174977H-18.24287605v-117.83174977h1060.4857521z m-176.74762535-235.66350091v117.83174977H158.5047493V335.25237466h706.9905014z m176.74762535-235.66350093v117.83175114H-18.24287605V99.58887373h1060.4857521z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextRight ? 'active' : '']" @click="inputAttr('textRight')")
            el-tooltip(effect="dark" content="右对齐" placement="bottom")
              <svg id="icon-text-align-left" viewBox="0 0 1024 1024"><path d="M747.66350093 806.57937513v117.83175114H-18.24287605v-117.83175114h765.90637698z m294.57937512-235.66349956v117.83174977H-18.24287605v-117.83174977h1060.4857521z m-294.57937512-235.66350091v117.83174977H-18.24287605V335.25237466h765.90637698z m294.57937512-235.66350093v117.83175114H-18.24287605V99.58887373h1060.4857521z"></path></svg>
        el-col(:span="6")
          .icon-text-block(:class="[isTextJustify ? 'active' : '']" @click="inputAttr('textJustify')")
            el-tooltip(effect="dark" content="两端对齐" placement="bottom")  
              <svg id="icon-text-align-justify" viewBox="0 0 1024 1024"><path d="M688.74762534 806.57937513v117.83175114H-18.24287605v-117.83175114h706.99050139z m353.49525071-235.66349956v117.83174977H-18.24287605v-117.83174977h1060.4857521z m0-235.66350091v117.83174977H-18.24287605V335.25237466h1060.4857521z m0-235.66350093v117.83175114H-18.24287605V99.58887373h1060.4857521z"></path></svg>
    .block(:style="{paddingTop: 0, paddingBottom: 0}")
      el-row 
        el-col(:span="8") 
          .des(:style="{color: '#333', paddingTop: '11px'}" class="no-user-select") 不透明度
        el-col(:span="16")
          el-slider(class="opactiy-slider" @mouseup.native="inputAttrBlur" @mousedown.native="inputAttrFocus" @input="opacityInput" :value="elementOpacity" :style="{marginRight: '10px'}")
    .block
      .des 尺寸与坐标
      .block-box
        .input-group
          .title  W
          .des-text
            el-input(
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @input="val => inputAttr('w', val)" 
              class="input-des-text" 
              size="small" 
              :value="elementStyle.width ? parseInt(elementStyle.width) : 0"
            )
        .input-group
          .title  H
          .des-text
            el-input(
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @input="val => inputAttr('h', val)" 
              class="input-des-text" 
              size="small" 
              :value="elementStyle.height ? parseInt(elementStyle.height) : 0"
            )
      .block-box
        .input-group
          .title  X
          .des-text
            el-input(
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @input="val => inputAttr('x', val)" 
              class="input-des-text" 
              size="small" 
              :value="elementStyle.left ? parseInt(elementStyle.left) : 0"
            )
        .input-group
          .title  Y
          .des-text 
            el-input(
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @input="val => inputAttr('y', val)" 
              class="input-des-text" 
              size="small" 
              :value="elementStyle.top ? parseInt(elementStyle.top) : 0"
            )
      .block-box
        .input-group
          .title  R
          .des-text
            el-input(
              @focus="inputAttrFocus" 
              @blur="inputAttrBlur"
              @input="val => inputAttr('r', val)" 
              class="input-des-text" 
              size="small" 
              :value="rotateDeg"
            )

</template>
<script>

import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
.font-size-input >>> .el-input__inner
  text-align left
  &:focus
    border-color #6ccfff
    outline 0
.input-des-text >>> .el-input__inner
  border-width 0
  font-size 16px
.opactiy-slider >>> .el-slider__button
  width 14px
  height 14px
  border 2px solid #6ccfff
.opactiy-slider >>> .el-slider__bar
  background-color #6ccfff
.side-right
  width 270px
  box-sizing border-box
  height 100%
  .modules
    width 100%
    height 100%
    display flex
    flex-direction column
    justify-content flex-end
    .block
      border-top 1px solid #ccc
      padding 10px
      .des
        font-size 14px
        color rgba(0, 0, 0, .4)
        padding-bottom 10px
      .text-font-size-block
        padding 5px 10px 15px 20px
      .text-font-color-block
        padding 5px 20px 15px 5px
      .block-box
        display flex
        .input-group
          margin 0 8px 8px 0
          padding 5px
          width 100px
          display flex
          flex 1
          align-items center
          border 1px solid #ccc
          border-radius 2px
          .title
            color #ccc
          .des-text
            color #333
      .icon-text-block
        padding 10px
        text-align center
        width 18px
        height 18px
        line-height 18px
        display inline-block
        cursor pointer
        svg
          fill #636c78
        &.active
          svg
            fill #6ccfff
</style>



